import { useEffect, useState } from "react";
import "./favoritos.css"
import { Link } from "react-router-dom";
import {toast} from 'react-toastify';
function Favortios(){
    const [filmes, setFilmes] = useState([])

    useEffect(()=>{
        const minhaLista = localStorage.getItem('@filmes')
        setFilmes(JSON.parse(minhaLista) || [])
    }, [])

    function excluirFilme(id){
        let filtroFilmes = filmes.filter((filme) =>{
            return(filme.id !== id)
        } )
        setFilmes(filtroFilmes)
        localStorage.setItem('@filmes', JSON.stringify(filtroFilmes))
        toast.success('Filme removido com sucesso!')
        //filtra atraves do id, pegando todos os filmes com a id diferente do atual, depois seta esses filmes na lista, menos o atual, removendo assim o filme da lista na parte visual

    }


    return(
        <div className="meus-filmes">
            <h1>Meus filmes </h1>

            {filmes.length === 0 && <span>Voc no possui filmes salvos!</span>}

            <ul>
                {filmes.map((filme) =>{
                   return( //sempre o que vai ser montado na tela apos o map tem que ta dentro do return
                    <li key={filme.id}>
                        <span>{filme.title}</span>
                        <div>
                            <Link to={`/filme/${filme.id}`}>Ver detalhes</Link>
                            <button onClick={() =>excluirFilme(filme.id)}>Excluir</button>
                        </div>
                    </li>
                   )
                    
                })}


            </ul>
        </div>
    )
}

export default Favortios;