import { useEffect, useState } from "react";
import { useParams, useNavigate, json } from "react-router-dom";
import "./filme.css"
import api from '../../services/api'
import {toast} from 'react-toastify'


function Filme(){
    const {id} = useParams(); //usado para pegar um determinado valor no endereo do site, um id por exemplo
    const navigate = useNavigate() //usado para redirecionar para um determinado local do site
    const [filme, setFilme] = useState({})
    const[loading, setLoading] = useState(true)
    useEffect(()=>{
        async function loadFilme(){
            await api.get(`/movie/${id}`,{
            params:{
                    api_key: '18497addb47cad24fae8210fea385b33',
                    language: 'pt-BR'
            }
        }
        )
        .then((response) =>{
            setFilme(response.data)
            setLoading(false)
        })
        .catch (()=>{
            console.log('filme no encontrado')
            navigate('/', {replace : true});
            return;
        })

        }
        loadFilme();


        return(
            console.log('componente desmontado')
        )
    }, [navigate, id])

    if(loading){
        return(
            <div className="filme-info">
                <h1>
                    Carregando Detalhes...
                </h1>
            </div>
        )
    }
    function salvarFilme(){
        const minhaLista = localStorage.getItem('@filmes') //faz a busca do filme em local storage

        let filmesSalvos = JSON.parse(minhaLista) || []; // verifica se ja existe uma lista, se no, cria uma vazia

        const hasFilmes = filmesSalvos.some((filmeSalvo) => filmeSalvo.id === filme.id) //verifica se o filme seleciobado ja est na lista
        
        if(hasFilmes) { //se o filme ja estiver na lista, exibe o aviso
           toast.warn('Esse filme ja est na sua lista!')
            return;
        }

       filmesSalvos.push(filme);
       localStorage.setItem('@filmes', JSON.stringify(filmesSalvos))
        toast.success('Filme salvo com sucesso! ')
    }

    return(
     
        <div className="filme-info">
            <h1>{filme.title}</h1>
           <img src={`https://image.tmdb.org/t/p/original/${filme.backdrop_path}`} alt={filme.title}/>
            <h3>Sinopse</h3>
            <span>{filme.overview}</span>
            <strong>Avaliao: {filme.vote_average} / 10</strong>
            <div className="area-buttons">
                <button onClick={salvarFilme}>Salvar</button>
                <button>
                <a target="blank"  rel='external' href={`https://www.youtube.com/results?search_query=${filme.title} trailer`}> {/*target="_blank" para que abra o link em uma nova aba | rel='external' para que o navegador entenda que o criador do site no tem controle sobre essa nova aba */}
                        Trailer
                    </a>
                </button>
            </div>
        </div>
    )
}

export default Filme;