import { Link } from 'react-router-dom';
import './erro.css'



function Erro(){
    return(
        <div className="not-found">
            <h1>
                404
            </h1>
            <h3>
                Pgina no encontrada!
            </h3>
            <span>
                <Link to={'/'}>Voltar para Filmes</Link>
            </span>
        </div>
    )
}

export default Erro;