import { wait } from "@testing-library/user-event/dist/utils";
import { useEffect, useState } from "react"; 
import api from "../../services/api";
import './home.css'

import { Link } from "react-router-dom";

// URL da Api : movie/now_playing?api_key=18497addb47cad24fae8210fea385b33&language=pt-BR

function Home(){
    const [filmes, setFilmes] = useState([])
    const [loading, setLoading] = useState(true)



    useEffect(()=>{
        async function loadFilmes(){
            const response = await  api.get('movie/now_playing', {
                params:{
                    api_key: '18497addb47cad24fae8210fea385b33',
                    language: 'pt-BR',
                    page: 1,
                }
            })
            setFilmes(response.data.results.slice(0,10))
        }

        loadFilmes();
        setLoading(false)
    }, [])

    if(loading){ //Exibe enquanto a paginas esta sendo carregada
        return(
            <div>
                <h2> 
                    Carregando filmes
                </h2>
            </div>
        )
    }
    return(
        <div className="container">
          <div className="lista-filmes">
          {
                filmes.map((filme)=>{
                    return(
                        <article key={filme.id}>
                            <strong>
                                {filme.title}
                            </strong>
                            <img src={`https://image.tmdb.org/t/p/original/${filme.poster_path}`} alt={filme.title}/>
                            <Link to={`/filme/${filme.id}`}>Acessar</Link>
                            

                        </article>
                    )
                })
            }

          </div>
            <footer className="footer">
                <div className="footer-content">
                    <p>&copy; {new Date().getFullYear()} PrimeFlix. Todos os direitos reservados.</p>
                </div>
            </footer>
        </div>
    )
}

export default Home;