import { Link } from "react-router-dom";
import { useState } from "react";
import './estilo.css'
function Header(){
    const  [nomeBtn, setNomeBtn] = useState('botaoHb')
    const [menu, setMenu] = useState('menu')
 
    function animacaoBtnHb(){
      if (nomeBtn === 'botaoHb'){
        setNomeBtn('clickBtnHb')
      
      }
      else(
        setNomeBtn('botaoHb')
      )

      if (menu === 'menu'){
        setMenu('menuResp')
      }
      else {
        setMenu('menu')
      }
    
    }

    return(
        <header>
            <Link className="logo" to='/'><img className="imgLogo" src={require('./images/logo.png')}/></Link>
            <div className={menu}>
                <Link className="itensMenu" to='/'> Home </Link>
                <Link className="itensMenu" to='/favoritos'> Favoritos</Link>
                <Link className="itensMenu" to=''> Sobre</Link>
                <Link className="itensMenu" to=''> Contatos</Link>
            </div>
            <div onClick={animacaoBtnHb} className={nomeBtn} >
            
                <div className="l1"></div>
                <div className="l2"></div>
                <div className="l3"></div>
            </div>
        </header>
    )
}

export default Header;